body {
  font-family: "OpenSans", "Arial", sans-serif;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 17px;
  font-weight: 400;
  line-height: 30px;
  color: #000000;
  background-color: #ffffff;
  position: relative;
}
a {
  text-decoration: none;
}
svg:not(:root) {
  overflow: hidden;
}
.visually-hidden {
  display: none;
}
.container {
  width: 320px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
  }
}
@media (min-width: 1150px) {
  .container {
    width: 1150px;
  }
}
@font-face {
