.site-list__logo {
  flex-basis: 86px;
  padding-top: 22px;
  height: 57px;
  text-align: center;
}
.site-list__item--cart,
.site-list__item--catalog,
.site-list__item--knitting,
.site-list__item--src {
  padding-top: 26px;
  padding-left: 75px;
  height: 52px;
  width: 245px;
