}
.site-list__link {
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  color: #000000;
  text-decoration: none;
  display: block;
}
.site-list__item--new,
.site-list__item--sale,
.site-list__item--delivery {
  display: none;
}
.icon-cart,
.icon-src {
  position: relative;
  margin-right: 26px;
  margin-bottom: -4px;
  margin-left: -43px;
  fill: #000000;
  opacity: 0.3;
}
.icon-cart polygon,
.icon-src polygon {
  fill: #ffffff;
}
.icon-cart:hover,
.icon-src:hover {
  color: #63d1bb;
}
.icon-cart:active,
.icon-src:active {
  color: #63d1bb;
  opacity: 0.3;
}
.site-list {
  font-weight: normal;
}
.site-list a[href]:hover {
